.social-login {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    button {
      color: white;
      width: 100%;
      max-width: 320px;
      display: flex;
      justify-content: center;
      align-items: center;
      &:not(:last-child) {
        margin-bottom: 15px;
      }
      span {
        margin-right: 10px;
        font-size: 20px;
      }
    }
    
    .social-login--github,:link{
      background-color: $black;
      color: white;
      text-decoration: none;

    }
    


    .social-login--facebook {
      background-color: #3a5998;
      color: white;
    }
  }